<template>
   <div class="patrimonio-tabela">
	    <div class="titulo">
			{{aba.nome}}
			<!--<va-checkbox v-if="aba.nome == 'Reports'" v-model="value" color="verde" label="Apenas reports em aberto"/>-->
		</div>
		<div class="tabela">
			<va-data-table hoverable :items="getObjetos" :columns="columns" :filter="getFiltro">
				<template v-if="aba.nome == 'Ativos'" #cell(coordinates)="{ rowIndex }">
					<va-button v-if="objetos[rowIndex].coordinates" color="secondary" flat icon="place" @click=maps(objetos[rowIndex].coordinates.latitude,objetos[rowIndex].coordinates.longitude) size="small" />
					<!--{{objetos[rowIndex].coordinates ? objetos[rowIndex].coordinates.latitude : ''}}
					{{objetos[rowIndex].coordinates ? objetos[rowIndex].coordinates.longitude : ''}}-->
				</template>
				<template v-if="aba.nome == 'Reports' || aba.nome == 'Serviços'" #cell(ativo)="{ rowIndex }">
					{{objetos[rowIndex].ativo ? objetos[rowIndex].ativo.description : ''}}
				</template>
				<template v-if="aba.nome == 'Reports'" #cell(created_at)="{ rowIndex }">
					{{timestamp(objetos[rowIndex].created_at)}}
				</template>
				<template v-if="aba.nome == 'Reports'" #cell(status)="{ rowIndex }">
					<va-icon size="medium" :name="getObjetos[rowIndex].status == '0' ? 'done' : 'close'" :color="getObjetos[rowIndex].status == '0' ? 'verde' : 'laranja'"/>
				</template>
				<template v-if="aba.nome == 'Serviços'" #cell(responsible)="{ rowIndex }">
					{{objetos[rowIndex].responsible ? objetos[rowIndex].responsible.name: ''}}
				</template>
				<template v-if="aba.nome == 'Serviços'" #cell(request)="{ rowIndex }">
					{{getRequest(objetos[rowIndex].request)}}
				</template>
				<template v-if="aba.nome == 'Serviços'" #cell(status_finish)="{ rowIndex }">
					<va-icon size="medium" :name="getObjetos[rowIndex].status_finish == 'Concluídos' ? 'done' : 'close'" :color="getObjetos[rowIndex].status_finish == 'Concluídos' ? 'verde' : 'laranja'"/>
				</template>
				<template #cell(actions)="{ rowIndex }">
					<va-button color="secondary" flat icon="edit" @click="update(objetos[rowIndex])" size="small" />
					<va-button color="secondary" flat icon="image" @click="getAllPhotos(objetos[rowIndex])" size="small" />
					<!--<va-button color="secondary" flat icon="visibility" @click="update(objetos[rowIndex])" size="small" />-->
				</template>
			</va-data-table>
		</div>
	  	<!--<div v-for="objeto in objetos" :key="objeto.id">
			<div style="padding: 10px;">{{objeto}}</div>
		</div>-->
   </div>
</template>

<script>
export default {
    name: 'PatrimonioTabela',
	props : ['aba', 'caption', 'objetos'],
	data () {
		return {
			value: true,
    	}
	},
	computed: {
		getFiltro(){
			return this.$store.getters['abas.getFiltro'][this.$route.path]
		},
		getObjetos(){
			return this.objetos.map((obj)=>{
				if (this.aba.nome == 'Ativos') return {
					...obj,
					categoria: obj.category ? obj.category.description : ''
				}
				else if (this.aba.nome == 'Serviços') return {
					...obj,
					status_finish: obj.finish ? 'Concluídos' : 'Em aberto',
					inicio: `${obj.dt_start_scheduled} `.replaceAll(':00 ',''),
					fim: `${obj.dt_end_scheduled} `.replaceAll(':00 ',''),
				}
				else return obj
			})
		},
		columns(){
			if(this.aba.nome == 'Ativos'){
				return [
						{ key: 'description' , label:'DESCRIÇÃO', sortable: true },
						{ key: 'patrimony' , label: 'N° PATRIMONIO', sortable: true },
						{ key: 'coordinates', label: 'LOCALIZAÇÃO'},
						{ key: 'categoria', label:'CATEGORIA', sortable: true },
						{ key: 'actions', label: 'AÇÕES'}
						
				]
			}
			else if(this.aba.nome == 'Reports'){
				return [
						{ key: 'anomaly' , label:'ANOMALIA', sortable: true },
						{ key: 'ativo' , label: 'ATIVO', sortable: true},
						{ key: 'criticality', label:'CRITICIDADE',  sortable: true },
						{ key: 'created_at', label:'CRIADO EM', sortable: true},
						{ key: 'status', label:'STATUS', sortable: true},
						{ key: 'actions', label: 'AÇÕES'}
					]
			}
			else if(this.aba.nome == 'Serviços'){
				return [
						{ key: 'request' , label:'REQUISIÇÃO', sortable: true},
						{ key: 'ativo' , label: 'ATIVO', sortable: true},
						{ key: 'priority', label:'PRIORIDADE', sortable: true},
						{ key: 'responsible', label:'RESPOSÁVEL', sortable: true},
						{ key: 'inicio', label:'Início', sortable: true},
						{ key: 'fim', label:'Fim', sortable: true},
						{ key: 'status_finish' , label:'STATUS',sortable: true},
						{ key: 'actions', label: 'AÇÕES'}
					]
			}
			else return []
		}
	},
	methods:{
		update(objeto){
			this.$store.dispatch('edit-modal.editShow', {
				objeto: objeto,
                callback: this.aba.callback
			})
		},
		async getAllPhotos(objeto){
			this.$store.dispatch('evidencias.show', objeto)
		},
		timestamp(time_str){
			var date = new Date(time_str)
			return date.toLocaleDateString('pt-BR')
		},
		maps(latitude, longitude){
			if (latitude != null && latitude != undefined && longitude != null && longitude != undefined){
				const url = `https://www.google.com/maps/place/${latitude}${longitude}/@${latitude}${longitude}`
				window.open(url, "_blank");
			}
		},
		getRequest(request){
			let slice = request.slice(0,50)
			if (slice == request) return request
			else return `${slice} ...`
		}
	}
}
</script>

<style scoped>
.patrimonio-tabela {
  margin-top: 10px;
  grid-area: patrimonio-tabela;
  background-color: var(--c1);
}

.titulo{
	background-color: var(--c4);
	padding: 6px;
	margin-bottom: 10px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	color: var(--branco)
}
.tabela{
	color: var(--c3)
}
</style>