<template>
<div class="user-updatecard">
	<div class="user-updatecard-title">
		Informações Pessoais
	</div>
	<div class="user-updatecard-content">
		<va-input class="user-updatecard-input" color="dark" v-model="usuario.name" label="NOME"/>
		<va-input class="user-updatecard-input" color="dark" v-model="usuario.email" label="EMAIL"/>
		<!--<va-input class="user-updatecard-input" color="dark" v-model="usuario.TELEGRAM" label="ID TELEGRAM">
			<template #appendInner>
				<va-popover message="Nesse campo não deve ser inserido o número de telefone! Para ativar o envio de mensagens: acesse o Telegram, procure por @FlexxsBot, inicie o chat enviando /start e insira nesse campo o ID recebido." color="secondary">
					<va-icon name="info" class="telegram-icon"/>
				</va-popover> 
			</template>
		</va-input>-->
		<va-button type="submit" @click="update" color="verde" text-color="cinza">Atualizar dados</va-button>
	</div>
</div>
</template>

<script>
import Usuario from '../../models/usuario'
export default {
	name: 'UserUpdateCard',
	data () {
		return {
			usuario: {}
		}
	},
    computed:{
		getUsuario(){
			return new Usuario(this.$store.getters['usuario.get'],this.axios)
		}
	},
	watch: {
		getUsuario(value){
			this.usuario = value
		}
	},
	methods:{
		load(){
			this.usuario = this.getUsuario
		},
	},
	mounted() {
		this.load()
	},
}
</script>

<style scoped>
.user-updatecard {
	grid-area: user-updatecard;
	background-color: var(--c1);
	margin-right: 10px;
}
.user-updatecard-content{
	width: 100%;
	padding: 30px;
}
.user-updatecard-title{
	background-color: var(--c4);
	width: 100%;
	color: white;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	padding: 15px;
}
.user-updatecard-input{
	width: 100% !important;
	margin-bottom: 10px;
}
.user-updatecard-select{
	color: white;
	margin: 20px 0;
}
.telegram-icon:hover{
	cursor: help;
}


@media only screen and (max-width: 600px) {
  .user-updatecard {
    margin-right: 0;
  }
}
</style>