module.exports = {
    'url': "https://px40-api.cme-eng.com.br",
	'color': ["#f8cb70", "#f85b40", "#4ea397",'#5470c6', '#91cc75', '#a60303', '#73c0de','#9a60b4', '#ea7ccc'],
    'model': class {

        filter(lista){
            return Object.keys(this).filter((key) => lista.includes(key))
                .reduce((cur, key) => { return Object.assign(cur, { [key]: this[key] })}, {});
        }

        filterCreate(){
            let lista = this.listaCriacao.filter(e=>typeof(e)=='object' && !e.readonly).map(e=>e.label)
            if(this.concat) lista = lista.concat(Object.values(this.concat))
            return this.filter(lista)
        }

        filterUpdate(){
            let objeto = {...this}
            // atributos que vem do backend
            delete objeto.ativo
            delete objeto.photos
            delete objeto.notes
            //---Ativos-------
            delete objeto.category
            delete objeto.company_id
            delete objeto.coordinates
            delete objeto.created_at
            delete objeto.parent_ativo
            delete objeto.photos
            delete objeto.sector
            delete objeto.updated_at
            //---Serviços-------
            delete objeto.custos
            delete objeto.effect
            delete objeto.fail_effect_id
            delete objeto.finished_at
            delete objeto.maintenance_type
            delete objeto.mao_obra
            delete objeto.paradas
            delete objeto.photos
            delete objeto.project
            delete objeto.responsible
            delete objeto.solicitacao
            delete objeto.subtasks
            //---Planos-------
            delete objeto.items
            delete objeto.tasks
            // atributos adicionados por nós
            delete objeto.concat
            delete objeto.allowRemove
            delete objeto.axios
            delete objeto.model_type
            delete objeto.listaAtualizacao
            delete objeto.listaCriacao
            delete objeto.listaDeSelecao
            delete objeto.todas_empresas
            console.log(objeto)
            return objeto
        }
    }
}