<template>
	<div class="geral">
		<div class="contatos">
			<img class="logoflexxs" src="@/assets/logo_px40_principal.png" />
			<va-inner-loading icon="autorenew" class="contatos-inputs" :loading='loading'>
				<div class="contatos-caption">Fale conosco</div>
				<div class="contatos-caption" style="font-size: 1.3em">Preencha o formulário e envie sua mensagem.</div>
				<va-input color="cinza" type="text" label="NOME" style="padding: 10px;" v-model="body.NOME" ></va-input>
				<va-input color="cinza" type="email" label="EMAIL" style="padding: 10px;" v-model="body.EMAIL" ></va-input>
				<va-input color="cinza" type="phone" label="TELEFONE" style="padding: 10px;" v-model="body.TELEFONE" ></va-input>
				<va-input color="cinza" type="textarea"  label="MENSAGEM" :min-rows="3" :max-rows="5" style="padding: 10px;"  v-model="body.MENSAGEM" />
				<va-button text-color="cinza" :rounded="false" color="verde" class="contatos-button" @click="enviar">Enviar</va-button>
			</va-inner-loading>
		</div>
		<FloatButton @click="back" type='vuestic' icon="home"/>
	</div>
</template>

<script>
import FloatButton from '@/utils/FloatButton'
export default {
	name: 'Contatos',
	components:{FloatButton},
	data: () => ({
        body: {},
		loading: false
    }),
	methods: {
        back(){
            this.$router.push('/');
        },
		async enviar(){
			try {
				this.loading=  true;
				await this.axios.post('https://www.flexxs.com.br:5002/api/faleconosco',{
					...this.body,
					LOCAL: 'px40'
				});
				this.$store.dispatch('toast.success', 'Mensagem enviada');
				this.body.NOME = null;
				this.body.EMAIL = null;
				this.body.TELEFONE = null;
				this.body.MENSAGEM = null;
				this.$router.push('/');
			} catch (err) {
				this.$store.dispatch('toast.error', err);
			}
			this.loading=  false;
        }
    }
}
</script>

<style scoped>

.geral{
    width: 100%;
}
.voltar{
    width: 100%;
	z-index: 2;
}

.contatos{
    width: 100%;
    height: 100vh;
    background-image: linear-gradient(0deg, #828691, var(--branco));
    z-index: -1;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.logoflexxs{
    width: 20%;
	padding: 30px 0px;
}
.contatos-inputs{
	width: 700px;
	padding: 10px;
	/*background-image: linear-gradient(0deg, #828691, var(--branco));*/
	background-color: rgba(0,0,0, 5%);
	border-radius: 15px;
}


.contatos-caption{
	width: 100%;
	padding: 10px;
	font-size: 2em;
	color: var(--cinza);
	text-align: center;
}

.contatos-button{
	width: 100%;
	margin-block: 20px;
}


@media only screen and (max-width: 800px) {
    .contatos-inputs{
		width: 100vw ;
		padding: 10px;
		/*background-image: linear-gradient(0deg, #828691, var(--branco));*/
		background-color: rgba(0,0,0, 0%);
		border-radius: 15px;
	}
	.logoflexxs{
		width: 70%;
		padding: 30px 0px;
	}

}

</style>